html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-close {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-next {
  width: 2em;
  height: 1em;
  fill: initial;
}

.icon-playlist {
  width: 1.3em;
  height: 1em;
  fill: initial;
}

.icon-playpause {
  width: 2.11em;
  height: 1em;
  fill: initial;
}

.icon-prev {
  width: 2em;
  height: 1em;
  fill: initial;
}

html, body {
  min-height: 100%;
}

body {
  background: #fff;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  line-height: 1.1;
}

.button {
  position: relative;
  background: transparent;
  width: 40px;
  height: 40px;
  z-index: 2;
  transition: transform .5s;
  text-align: center;
  font-size: 0;
}

.button:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.button:active .icon {
  transform: scale(0.9);
}

.button:hover:after {
  opacity: 1;
}

.button:after {
  content: '';
  position: absolute;
  left: -5px;
  top: -5px;
  bottom: -5px;
  right: -5px;
  background: rgba(153, 153, 153, 0.8);
  border-radius: 50%;
  opacity: 0;
  transition: opacity .3s;
  z-index: -1;
}

.progress svg {
  overflow: visible;
}

.progress circle {
  fill: none;
}

.progress__trigger {
  stroke: transparent;
  stroke-width: 10px;
  stroke-dashoffset: 1382px;
  stroke-dasharray: 0;
  cursor: pointer;
}

.progress__main {
  stroke: #fff;
  stroke-width: 3px;
  /* 2 * Math.PI * radius + 1  */
  stroke-dashoffset: 1382px;
  stroke-dasharray: 1382px;
}

.progress__main:hover {
  stroke: red;
}

.progress__border {
  stroke: #fff;
  stroke-width: 1px;
}

.playlist {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.playlist__top {
  padding-top: 1px;
  margin-top: -1px;
  position: relative;
  flex-shrink: 0;
  height: 61px;
  border-bottom: 1px solid #fff;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-x: hidden;
}

.playlist__top:after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 1px;
  height: 1px;
  background: #fff;
}

.playlist__close .icon {
  vertical-align: middle;
  fill: #fff;
  font-size: 26px;
  line-height: 1;
}

.playlist__list {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  padding: 10px 0;
  border-bottom: 1px solid #fff;
  overflow: auto;
}

.playlist__list-in {
  margin: auto;
}

.playlist__list a {
  display: block;
  color: #fff;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  transition: color .3s ease;
}

.playlist__list a:not(:last-child) {
  margin-bottom: 30px;
}

.playlist__list a.is-active {
  color: #fdff6d;
}

.playlist__list a strong {
  display: block;
  font-size: 1.125em;
  font-weight: 500;
}

.playlist__progress {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: #fff;
  transform: translateX(-100%);
}

.playlist__progress--trigger {
  transform: translateX(0);
  background: none;
  cursor: pointer;
  z-index: 2;
}

.controls {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.controls__progress {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.controls .icon {
  fill: #fff;
  font-size: 16px;
  display: inline-block;
  vertical-align: middle;
  transition: transform .3s;
}

.controls__playlist {
  position: absolute;
  top: 10px;
  left: 50%;
  margin-left: -20px;
}

.controls__playlist .icon {
  font-size: 20px;
}

.controls__next {
  position: absolute;
  right: 10px;
  top: 50%;
  margin-top: -20px;
}

.controls__pause {
  position: absolute;
  left: 50%;
  bottom: 10px;
  margin-left: -20px;
}

.controls__prev {
  position: absolute;
  left: 10px;
  top: 50%;
  margin-top: -20px;
}

.info {
  height: 100%;
  width: 100%;
  color: #fff;
  text-align: center;
  padding-top: 100px;
}

.info__time {
  font-size: 55px;
  margin-bottom: 30px;
}

.info__name {
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 4px;
}

.info__author {
  font-size: 16px;
}

.player {
  position: relative;
  height: 100vh;
  background: #333 no-repeat center center / cover;
}

.player:after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
}

.player__in {
  position: relative;
  z-index: 2;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.player__main {
  position: relative;
  width: 440px;
  height: 440px;
  perspective: 1000px;
}

.player__main.playlist-open .player__body {
  opacity: 0;
  visibility: hidden;
  transform: rotateX(-40deg);
}

.player__main.playlist-open .player__playlist {
  opacity: 1;
  visibility: visible;
  transform: rotateX(0deg);
}

.player__body, .player__playlist, .player__progress {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.player__body {
  transition: all .3s;
}

.player__playlist {
  opacity: 0;
  visibility: hidden;
  transform: rotateX(40deg);
  transition: all .3s;
}

.player__controls {
  width: 100%;
  height: 100%;
}

.player__info {
  position: absolute;
  width: 320px;
  height: 320px;
  left: 50%;
  top: 50%;
  margin-left: -160px;
  margin-top: -160px;
  border: 1px solid #fff;
  border-radius: 50%;
}

.player__playlist {
  position: absolute;
  top: 0;
  left: 0;
}
